import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

import FormParameter from "./Component/FormParameter";
import { getListParameter } from "../actions/parameterTransaksiAction";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { reset } from "redux-form";
import paginationFactory from "react-bootstrap-table2-paginator";
import { berhasil, gagal, LogData } from "../Alert";
import { AxiosMasterDelete, AxiosMasterPost } from "../Axios";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("transaksi-kas/delete/" + kode)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getListParameter())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}
const maptoState = (state) => {
  return {
    kodeParameter: state.provinsi.parameterTransaksi,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class ParameterTransaksi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ParameterTransaksi: [],
      response: "",
      columns: [
        {
          dataField: "kategori_transaksi",
          text: "Kode Parameter Transaksi",
          headerStyle: () => {
            return { width: "70%" };
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          headerStyle: () => {
            return { width: "30%" };
          },
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_3"
                  onClick={() =>
                    hapus(this.props.dispatch, row.kategori_transaksi)
                  }
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListParameter());
  }

  handleSubmit(hasil) {
    let data = {
      kategori_transaksi: hasil.kodeParameter,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("transaksi-kas/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => this.props.dispatch(getListParameter()))
          .then(() => this.props.dispatch(reset("Parameter")))
      )
      .catch((err) =>
        gagal("Data Gagal Ditambah!")
          .then(() =>
            LogData("Data Gagal Ditambah Di Parameter Transaksi", data, err)
          )
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Parameter Transaksi, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data Parameter Transaksi yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title=" Data ParameterTransaksi" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    <FormParameter
                      onSubmit={(data) => this.handleSubmit(data)}
                      onSend={this.props.onSend}
                    />
                  }
                  title="Tambah Data Parameter Transaksi"
                />
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          onClick={() => this.tambahModal()}
                          data-tut="reactour_1"
                        >
                          Tambah Data Parameter
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                <div data-tut="reactour_2">
                  {this.props.kodeParameter ? (
                    <ToolkitProvider
                      keyField="kodeParameter"
                      data={this.props.kodeParameter}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right mt-5 mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : null}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(ParameterTransaksi);
