import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { renderField } from "../../renderField";

const validationkodeParameter = (value) => {
  const errors = {};
  if (!value.kodeParameter) {
    errors.kodeParameter = "Tidak Boleh kosong";
  }
  return errors;
};
class FormParameter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kategori: "",
    };
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3 col-md-3"></div>
          <div className="col-md-6 text-center">
            <Field
              name="kodeParameter"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Parameter"
            />
          </div>
          <div className="col-lg-3 col-md-3"></div>
          <div className="col-md-12 col-lg-12">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Selesai
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormParameter = reduxForm({
  form: "Parameter",
  validationkodeParameter,
  enableReinitialize: true,
})(FormParameter);
export default connect()(FormParameter);
